html,
body {
  cursor: url('../../images/skeleton-cursor.png') 8 0, auto;
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
  background-color: #1a0f00;
  background-image: url('../../images/Background.png');
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

/* Main container */
.Grimoire {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: end;
  image-rendering: pixelated;
  font-family: 'Press Start 2P', monospace;
  color: #f3e9d2;
  text-align: center;
}
.grimoire-title {
  position: absolute ;
  top: 2%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10001;
  font-size: 32px;
  font-family: 'Press Start 2P', monospace;
  color: #f3e9d2;
  text-shadow: 2px 2px #000;
  pointer-events: none;
}

.title-shifted {
  position: absolute ;
  top: -20%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10001;
  font-size: 32px;
  font-family: 'Press Start 2P', monospace;
  color: #f3e9d2;
  text-shadow: 2px 2px #000;
  pointer-events: none;
}

/* Altar layout */
.altar {
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Candle image styling */
.candle-img {
  position: relative;
  width: 50%;
  height: 60%;
  image-rendering: pixelated;
  z-index: 2;
}

.candle-wrapper {
  position: relative;
  width: 20%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: self-start;
}

/* Glow effect */
.candle-wrapper::after {
    content: "";
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    width: 20%;
    height: 10%;
    border-radius: 50%;
    background: radial-gradient(circle, rgba(255, 255, 150, 0.8) 0%, rgba(255, 200, 0, 0.4) 60%, transparent 100%);
    animation: candleGlow 1.5s infinite ease-in-out;
    pointer-events: none;
    z-index: 1;
}

@keyframes candleGlow {
  0%, 100% {
    opacity: 0.6;
    transform: translateX(-50%) scale(1);
    box-shadow: 0 0 6px rgba(255, 255, 150, 0.6), 0 0 12px rgba(255, 200, 0, 0.4);
  }
  50% {
    opacity: 1;
    transform: translateX(-50%) scale(1.2);
    box-shadow: 0 0 10px rgba(255, 255, 150, 0.8), 0 0 20px rgba(255, 200, 0, 0.6);
  }
}

/* Book image styling */
.book-img {
  width: 40%;
  height: auto;
  image-rendering: pixelated;
  filter: drop-shadow(2px 2px 0 #000) drop-shadow(-2px -2px 0 #000) brightness(1);
  transition: filter 0.3s ease;
}

/* Hover effect: brighten the book */
.book-img:hover {
  filter: drop-shadow(2px 2px 0 #000) drop-shadow(-2px -2px 0 #000) brightness(1.2);
}
/* Optional content below the altar */
.content {
  margin-top: 20%;
  max-width: 80%;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-image: url('../../images/GrimoireOpenedBook.png'); /* Your open book image */
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: riseFromBook 0.6s ease-out;
}

@keyframes riseFromBook {
  0% {
    transform: scale(0.5) translateY(100px);
    opacity: 0;
  }
  100% {
    transform: scale(1) translateY(0);
    opacity: 1;
  }
}

.modal-content {
  background: rgba(0, 0, 0, 0.6);
  height: 50%;
  width: 60%;
  padding: 40px;
  border-radius: 8px;
  text-align: center;
  color: #f3e9d2;
  font-family: 'Press Start 2P', monospace;
  display: flex;
  flex-direction: row;
}

.modal-menu {
  width: 15%;
  min-width: 200px; /* optional: ensures usability on small screens */
  background: rgba(0, 0, 0, 0.6);
}

.post-section {
  width: 80%;
  padding: 1rem;
  overflow-y: auto;
}
.modal-menu {
  background: rgba(0, 0, 0, 0.6);
  color: #f3e9d2;
  font-family: 'Press Start 2P', monospace;
  padding: 20px;
  box-shadow: 2px 0 6px #000;
  image-rendering: pixelated;
  transition: width 0.3s ease;
  overflow: hidden;
  width: 10%;
}

.menu-toggle {
  background: none;
  border: none;
  color: #f3e9d2;
  font-size: 20px;
  cursor: pointer;
  margin-bottom: 20px;
}

.modal-menu ul {
  list-style: none;
  padding: 0;
}

.modal-menu li {
  margin-bottom: 16px;
}

.modal-menu li button {
  background: none;
  border: none;
  color: inherit;
  font-family: inherit;
  cursor: pointer;
  text-align: left;
  width: 100%;
}

.modal-main {
  flex-grow: 1;
  padding: 40px;
  background: rgba(0, 0, 0, 0.6);
  color: #f3e9d2;
}

.post-section {
  padding: 1rem;
  flex: 1;
  overflow-y: auto;
}

.post-content article {
  margin-bottom: 2rem;
  border-bottom: 1px solid #ccc;
}